import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

import { Indicator } from 'mint-ui'
import { Toast } from 'mint-ui'
Vue.use(Vuex)

const state = {
    casualList: [],  // 
    hotNav: [], // ,
    hotGoodsList: [],  // 
    recommendGoodsList:[],  // 
    categoryAndGoods:{},  //
    loginInfo: JSON.parse(localStorage.getItem('loginInfo')) || {  // 
        isLogin: false,
        userInfo:null,
        userName:'',
    },
    ordersList:[],  // 
    addressList: [], // 
    pdc:{}, // 
    shopCartData:[], // 
    defaultAdderss:{},  // 
    editorAddressInfo:{}, // 
}   

const getters = {
    getCasualList() { // 
        return state.casualList
    },
    getHotNav() { // 
        return state.hotNav
    },
    getHotGoodsList() { // 
        return state.hotGoodsList
    },
    getRecommendGoodsList() { // 
        return state.recommendGoodsList
    },
    getCategoryAndGoods() {  // 
        return state.categoryAndGoods
    },
    getLoginInfo() {  // 
        return state.loginInfo
    },
    getAllOrdersList() { // 
        return state.ordersList
    },
    getAddressList() {  // 
        return state.addressList
    },
    getPdc() {  // 
        return state.pdc
    },
    getShopCartData() {  // 
        return state.shopCartData
    },
    getDefaultAddress() {  // 
        return state.defaultAdderss
    },
    editorAddressInfo() {  // 
        return state.editorAddressInfo
    }
}

const actions = {
    getCasualList(ctx) {  // 
        axios.get('getHomeData').then((res) =>{
            ctx.commit('getCasualList', res.data.slideShowList)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getHotNav(ctx) { // 
        axios.get('getHomeData').then((res) =>{
           ctx.commit('getHotNav',res.data.homeNavs)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getHotGoodsList(ctx,id){  // 
        var params = new URLSearchParams
        params.append('id',id)
        axios.post('getActivityGoods',params).then((res) =>{
            ctx.commit('getHotGoodsList',res.data.goodsList)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getRecommendGoodsList(ctx, id) {  // 
        var params = new URLSearchParams
        params.append('id',id)
        axios.post('getActivityGoods',params)
            .then((res) =>{
                ctx.commit('getRecommendGoodsList', res.data.goodsList)
            })
            .catch((e) =>{
                console.log(e)
            })
    },
    getCategoryAndGoods(ctx) {  // 
        axios.post('getCategoryAndGoods').then((res) =>{
            ctx.commit('getCategoryAndGoods', res.data)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getVerification(ctx,obj) {  // 
        axios.get('getCode',{
            params: {
                mobile: obj
            }
        }).then((res) =>{
            console.log(''+res.data.code)
            var info = {
                code: res.data.code,
                mobile: obj
            }
            ctx.commit('getVerification',info)
        }).catch((e) =>{
            console.log(e)
        })
    },
    loginInfo(ctx,obj) {  // 
        axios.get('login', {
            params: {
                userPhone: obj.mobile
            }
        }).then((res) =>{
            ctx.commit('loginInfo', res.data)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getAllOrdersList(ctx, obj) { // 
        // 
        axios.get('getOrderList',{
            params: {
                uuid: obj.uuid,
                orderStatus: obj.orderStatus
            }
        }).then((res) =>{
            //console.log(res)
            ctx.commit('getAllOrdersList', res.data)
        }).catch((e) =>{
            console.log(e)
        })
    },
    getAddress(ctx,obj) {  // 
        axios.get('getExpressAddressByUUID', {
            params: {
                uuid: obj
            }
        }).then((res) =>{
            ctx.commit('getAddress', res.data)
        }).catch((e) =>{
            console.log(e)
        })
    },
    pdc(ctx,pdc) {  // 
        ctx.commit('pdc',pdc)
    },
    logout(ctx,obj) {  // 
        ctx.commit('logout', obj)
    },
    getShopCartData(ctx) { // 
        //    uuid
        axios.get('getShoppingCartListByUUID', {
            params: {
                uuid: state.loginInfo.userInfo.uuid
            }
        }).then((res) =>{
            ctx.commit('getShopCartData', res.data)
        }).catch((e) =>{
            console.log(e)
        })
    },
    _delGoods(ctx,arr) {  // 
        arr.forEach((item,index) =>{
            if(item.cartId) {
                axios.get('deleteShoppingCart',{
                    params: {
                        id: item.cartId
                    }
                }).then((res) =>{
                    if(res.data == 'ok') {
                        this._actions.getShopCartData[0]()
                    }
                })
            }
        })
    },
    settlement(ctx, obj) { // 
        var url = 'createOrder'
        axios.post(url, obj.settlementInfo).then((res) =>{
            if(res.data.code == 1) {
                // 
                Indicator.open('')
                // 
                obj.shopCartArr.forEach((item,index) =>{
                    if(item.cartId) {
                        axios.get('deleteShoppingCart',{
                            params: {
                                id: item.cartId
                            }
                        }).then((res) =>{
                            if(res.data == 'ok') {
                                this._actions.getShopCartData[0]()
                                Indicator.close()
                                Toast({
                                    message: '',
                                    iconClass: 'iconfont icon-duigou'
                                })
                            }
                        })
                    }
                })
            }else if(res.data.code == 3) {
                //  
                Indicator.open('')
                // 
                obj.shopCartArr.forEach((item,index) =>{
                    if(item.cartId) {
                        axios.get('deleteShoppingCart',{
                            params: {
                                id: item.cartId
                            }
                        }).then((res) =>{
                            if(res.data == 'ok') {
                                this._actions.getShopCartData[0]()
                                Indicator.close()
                                
                                Toast({
                                    message: '',
                                    iconClass: 'iconfont icon-cha',
                                    duration: 4000
                                })
                            }
                        })
                    }
                })
            }
            
        }).catch((e) =>{
            console.log(e)
        })
    },
    editorAddress(ctx, info) {  // 
        ctx.commit('editorAddress', info)
    },
    updateExpressAddress(ctx,obj) {  // 
        axios.post('updateExpressAddress',obj).then((res) =>{
            if(res.data) {
                Toast({
                    message: '',
                    iconClass: 'iconfont icon-duigou'
                })
                history.go(-1)
            }
        })
    },
    updateUserInfo(ctx,obj) {  // 
        axios.post('updateUser',obj).then((res) =>{
            ctx.commit('updateUserInfo', res.data)
        })
    }
}

const mutations = {
    getCasualList(state,data){ // 
        state.casualList = data
    },
    getHotNav(state, data) {  // 
        state.hotNav = data
    },
    getHotGoodsList(state, data) {  // 
        state.hotGoodsList = data
    },
    getRecommendGoodsList(state, data) { // 
        state.recommendGoodsList = data
    },
    getCategoryAndGoods(state, data) { // 
        state.categoryAndGoods = data
    },
    getVerification(state, data) { // 
        state.loginInfo.info = data
    },
    loginInfo(state, data) {  // 
        state.loginInfo.isLogin = true
        if(state.loginInfo.isLogin) {
            state.loginInfo.msg = ''
            state.loginInfo.userInfo = data
        }
        // 
        localStorage.setItem('loginInfo','')
        localStorage.loginInfo = JSON.stringify(state.loginInfo)
    },
    getAllOrdersList(state, data) {  // 
        //  
        /*
            0. 
            1. 
            2. 
            3. 
            4. 
            5. 
        */
        //console.log(data)
        var test = [
            {code:0,name:''},
            {code:1,name:''},
            {code:2,name:''},
            {code:3,name:''},
            {code:4,name:''},
            {code:5,name:''},
        ]
        for(var i=0;i<data.length;i++) {
            for(var k=0;k<test.length;k++) {
                if(data[i].orderStatus == test[k].code) {
                    for(var j=0;j<data[i].orderDetailList.length;j++) {
                        data[i].orderDetailList[j].status = test[k].name
                    }
                 }   
            }
        }
        state.ordersList = data
    },
    getAddress(state, data) {  // 
        state.addressList = data
        // 
        for(var i=0;i<state.addressList.length;i++) {
            if(state.addressList[i].flag) {
                state.defaultAdderss = state.addressList[i]
                return
            }
        }
    }, 
    pdc(state,pdc) {  // 
        state.pdc = pdc
        state.pdc.isShow = false
    },
    logout(state,obj) { // 
        state.loginInfo.isLogin = obj
    },
    getShopCartData(state, data) { // 
        data.forEach(element => {
            element.isBuy = false
        });
        
        state.shopCartData = data
    },
    editorAddress(state, info) {  // 
        state.editorAddressInfo = info
    },
    updateUserInfo(state,info) {  // 
        state.loginInfo.userInfo = info
        localStorage.loginInfo = JSON.stringify(state.loginInfo)
        Toast({
            message: '',
            iconClass: 'iconfont icon-duigou'
        })
    }
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})


